@import url(https://fonts.googleapis.com/css2?family=Yomogi&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Yomogi', cursive;
}
.mainDiv{
  width: 100%;
  height: 100vh;
  background-image: linear-gradient(rgba(0,0,0,0.1), rgba(0,0,0,0.5)), url('https://picsum.photos/800/800');
  background-repeat: no-repeat;
  background-position: center;
  background-size: 100%;
}
.content_area{
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}
.content{
  text-align: center;
  background-color: #fff;
  opacity: .7;
  padding: 100px 180px;
  border-top-left-radius: 3.5rem;
  border-bottom-right-radius: 3.5rem;
}
.greetMsg{
  font-size: 2.2rem;
  font-weight:900;
  background: linear-gradient(90deg, rgba(255,0,219,1) 32%, rgba(255,244,0,1) 42%, rgba(14,9,121,1) 71%, rgba(255,55,0,1) 88%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.greetMsg::before{
  content: 'Hye ';
}
.content > .time, .date{
  padding: 6px 0;
  font-weight: 900;
  font-size: 1.3rem;
  background: linear-gradient(90deg, rgba(161,103,20,1) 43%, rgba(0,74,255,1) 88%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.day{
  background-color: rgba(206,107,354,0.3);
  border-radius: 2rem;
}
.todayIs{
  font-weight: 900;
  background: linear-gradient(90deg, rgba(34,161,20,1) 33%, rgba(255,162,0,1) 88%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.devInfo{
  right: 0;
  bottom: 0;
  padding-top: 100px;
  padding-bottom: 40px;
  padding-left: 100px;
  position: absolute;
  background-color: rgba(255,255,255,1);
  -webkit-clip-path: circle(58.1% at 81% 90%);
          clip-path: circle(58.1% at 81% 90%);
}
.devInfo > a{
  font-size: 1.2rem;
  font-weight: 900;
  text-decoration: none;
  background: rgb(11,1,153);
  background: linear-gradient(90deg, rgba(11,1,153,1) 33%, rgba(249,0,0,1) 88%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.devInfo::after{
  content: 'Web Developer';
  font-size: 1rem;
  -webkit-animation: txtAnimation 3s linear infinite;
          animation: txtAnimation 3s linear infinite;
}
@-webkit-keyframes txtAnimation {
  0%{
    background: linear-gradient(90deg, rgba(11,1,153,1) 56%, rgba(51,249,0,1) 65%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }
  50%{
    background: linear-gradient(90deg, rgba(0,166,167,1) 56%, rgba(2,0,153,1) 65%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }
  100%{
    background: linear-gradient(90deg, rgba(0,3,167,1) 9%, rgba(16,153,0,1) 39%, rgba(153,64,0,1) 75%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }
}
@keyframes txtAnimation {
  0%{
    background: linear-gradient(90deg, rgba(11,1,153,1) 56%, rgba(51,249,0,1) 65%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }
  50%{
    background: linear-gradient(90deg, rgba(0,166,167,1) 56%, rgba(2,0,153,1) 65%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }
  100%{
    background: linear-gradient(90deg, rgba(0,3,167,1) 9%, rgba(16,153,0,1) 39%, rgba(153,64,0,1) 75%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }
}
